import { Booking } from '@/models/booking.dto';
import { BookingListingService } from '@/services/api/booking_listing.service';
import { CommonService } from '@/services/api/common.service';
import { PropertyService } from '@/services/api/property.service';
import app_store from '@/stores/app.store';
import { formatAmount, getUserPrefernce } from '@/utils/utils';
import { Component, Fragment, Host, Listen, Prop, State, h } from '@stencil/core';
import axios from 'axios';
import { differenceInCalendarDays, format, isBefore } from 'date-fns';

@Component({
  tag: 'ir-booking-listing',
  styleUrl: 'ir-booking-listing.css',
  scoped: true,
})
export class IrBookingListing {
  @Prop() propertyid: number;
  @Prop() baseUrl: string;
  @Prop() language: string;
  @Prop() headerShown: boolean = true;
  @Prop() footerShown: boolean = true;
  @Prop() maxPages: number = 10;

  @State() isLoading = false;
  @State() token: string;
  // =
  // 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..jmwdoa7x9p6RbuR9RN8WN5OTGi6o_GBuqLRu1AkD4PE';
  @State() bookings: Booking[] = [];
  @State() currentPage = 1;
  @State() total_count = 1;

  private bookingListingService = new BookingListingService();
  private commonService = new CommonService();
  private propertyService = new PropertyService();

  async componentWillLoad() {
    axios.defaults.baseURL = this.baseUrl;
    if (!this.propertyid) {
      throw new Error('missing property id');
    }
    getUserPrefernce();
    if (this.token) {
      console.log('token');
      this.initializeServices();
      this.initializeApp();
    } else {
      const token = await this.commonService.getBEToken();
      console.log(token);
      if (token) {
        app_store.app_data.token = token;
      }
      // this.initializeServices();
      // this.initializeApp();
    }
  }
  initializeServices() {
    this.bookingListingService.setToken(this.token);
    this.propertyService.setToken(this.token);
    this.commonService.setToken(this.token);
  }
  async initializeApp() {
    try {
      this.isLoading = true;
      await Promise.all([
        this.propertyService.getExposedProperty({
          id: this.propertyid,
          language: app_store.userPreferences?.language_id || 'en',
          aname: '',
          perma_link: '',
        }),
        this.getBookings(),
        this.commonService.getExposedLanguage(),
      ]);
    } catch (error) {
      console.error(error);
    } finally {
      this.isLoading = false;
    }
  }
  async getBookings() {
    const start_row = this.currentPage === 1 ? 0 : this.currentPage * this.maxPages;
    const end_row = start_row + this.maxPages;
    const { bookings, total_count } = await this.bookingListingService.getExposedGuestBookings({ property_id: this.propertyid, start_row, end_row, total_count: 0 });
    this.bookings = bookings;
    this.total_count = total_count;
    return bookings;
  }
  handleAuthFinish(e: CustomEvent) {
    e.stopImmediatePropagation();
    e.stopPropagation();
    const { token, state } = e.detail;
    console.log(token, state);
    if (state === 'success') {
      this.token = token;
      this.initializeServices();
      this.initializeApp();
    }
  }
  getBadgeVariant(code: string) {
    if (code === '001') {
      return 'pending';
    } else if (code === '002') {
      return 'success';
    }
    return 'error';
  }
  @Listen('pageChange')
  async handlePageChange(e: CustomEvent<number>) {
    e.stopImmediatePropagation();
    e.stopPropagation();
    this.currentPage = e.detail;
    this.getBookings();
  }
  render() {
    if (!this.token) {
      return (
        <Host>
          <main class="flex h-screen flex-col  justify-center">
            <div class="mx-auto w-full max-w-md px-4">
              {/* <ir-signin></ir-signin> */}
              <ir-auth enableSignUp={false} onAuthFinish={this.handleAuthFinish.bind(this)}></ir-auth>
            </div>
          </main>
        </Host>
      );
    }
    if (this.isLoading) {
      return (
        <div class="grid h-screen w-full place-content-center">
          <div class="page-loader"></div>
        </div>
      );
    }
    const totalPages = Math.round(this.total_count / this.maxPages);
    return (
      <Host>
        {this.headerShown && <ir-nav isBookingListing website={app_store.property?.space_theme.website} logo={app_store.property?.space_theme?.logo}></ir-nav>}
        <main class={'p-4'}>
          <div class="ir-table-container mx-auto hidden max-w-6xl overflow-x-hidden shadow-md md:block">
            <div class="flex flex-col gap-2.5 px-[20px] py-[16px] md:flex-row md:items-center md:justify-between ">
              <h2 class="text-lg font-semibold leading-none tracking-tight">Bookings</h2>
              {/* <input type="text" placeholder="Search..." class="w-full max-w-sm rounded-md border border-solid border-gray-300 px-4 py-1.5 " /> */}
            </div>
            <div class="max-w-full overflow-x-auto ">
              <table class="ir-table">
                <thead>
                  <tr class="ir-table-header">
                    <th class="ir-table-head">Status</th>
                    <th class="ir-table-head">Booking reference</th>
                    <th class="ir-table-head">Booking date</th>
                    <th class="ir-table-head">Check-in</th>
                    <th class="ir-table-head">Duration</th>
                    <th class="ir-table-head">Total price</th>
                    <th class="ir-table-head sr-only">pay now</th>
                  </tr>
                </thead>
                <tbody>
                  {this.bookings?.map(booking => {
                    const totalNights = differenceInCalendarDays(new Date(booking.to_date), new Date(booking.from_date));
                    return (
                      <tr class="ir-table-row" key={booking.booking_nbr}>
                        <td class="ir-table-cell">{<ir-badge label={booking.status.description} variant={this.getBadgeVariant(booking.status.code)}></ir-badge>}</td>
                        <td class="ir-table-cell">{booking.booking_nbr}</td>
                        <td class="ir-table-cell">{format(new Date(booking.booked_on.date), 'dd-MMM-yyyy')}</td>
                        <td class="ir-table-cell">{format(new Date(booking.from_date), 'EEE, dd MMM yyyy')}</td>
                        <td class="ir-table-cell">
                          {totalNights} {totalNights > 1 ? 'nights' : 'night'}
                        </td>
                        <td class="ir-table-cell">{formatAmount(booking.total, booking.currency.code)}</td>
                        <td class="ir-table-cell">
                          <ir-menu
                            data={[
                              { id: 1, item: 'Cancel Booking' },
                              { id: 2, item: 'Pay $50 to guarentee' },
                            ]}
                          >
                            <ir-button slot="menu-trigger" variants="icon" iconHeight={16} iconWidth={16} removeIconClassName iconName="elipse_vertical"></ir-button>
                          </ir-menu>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
            <div class="px-[20px] py-[16px] ">
              <ir-pagination total={totalPages} current={this.currentPage}></ir-pagination>
            </div>
          </div>
          <section class={'space-y-4 md:hidden'}>
            <h2 class="pb-2 text-lg font-semibold leading-none tracking-tight">Bookings</h2>
            {this.bookings?.map(booking => {
              const totalNights = differenceInCalendarDays(new Date(booking.to_date), new Date(booking.from_date));
              return (
                <div class="relative flex flex-col space-y-1.5 rounded-xl  bg-gray-100 p-6 text-sm " key={booking.booking_nbr}>
                  <ir-menu
                    class="absolute right-2 top-2"
                    data={[
                      { id: 1, item: 'Cancel Booking' },
                      { id: 2, item: 'Pay $50 to guarentee' },
                    ]}
                  >
                    <ir-button slot="menu-trigger" variants="icon" iconHeight={16} iconWidth={16} removeIconClassName iconName="elipse_vertical"></ir-button>
                  </ir-menu>
                  <div class="flex items-center justify-between text-base">
                    <h3 class=" font-semibold leading-none tracking-tight">Booking number: #{booking.booking_nbr}</h3>
                    <p>{formatAmount(booking.total, booking.currency.code)}</p>
                  </div>
                  <p>
                    <span class="font-medium">Booked on: </span>
                    {format(new Date(booking.booked_on.date), 'dd-MMM-yyyy')}
                  </p>
                  <p>
                    <span class="font-medium">Check-in: </span>
                    {format(new Date(booking.from_date), 'EEE, dd MMM yyyy')}
                  </p>
                  <p>
                    <span class="font-medium">Duration: </span>
                    {totalNights} {totalNights > 1 ? 'nights' : 'night'}
                  </p>
                  <p class="flex items-center">
                    <span class="font-medium">Status: </span>
                    {<ir-badge backgroundShown={false} label={booking.status.description} variant={this.getBadgeVariant(booking.status.code)}></ir-badge>}
                  </p>
                  <div class="mt-2.5 flex items-center justify-end gap-4">
                    {isBefore(new Date(), new Date(booking.from_date)) && (
                      <Fragment>
                        <ir-button variants="outline" label="Cancel Booking"></ir-button>
                        <ir-button label="Pay $40 to guarentee"></ir-button>
                      </Fragment>
                    )}
                  </div>
                </div>
              );
            })}
            <ir-pagination total={totalPages} current={this.currentPage}></ir-pagination>
          </section>
        </main>
        {this.footerShown && <ir-footer></ir-footer>}
      </Host>
    );
  }
}
